:root {
  --Primary-100: 230, 35%, 7%;
  --Primary-200: 231, 77%, 90%;
  --Primary-300: 0, 0%, 100%;
}

:root {
  --ff-100: "Bellefair", serif;
  --ff-200: "Barlow Condensed", sans-serif;
  --ff-300: "Barlow", sans-serif;
}

h1,
h2,
h3,
h4 {
  font-family: var(--ff-100);
}

h1 {
  font-size: clamp(5rem, 2.47rem + 10.78vw, 9.375rem);
  color: hsl(var(--Primary-300));
}

h2 {
  font-size: clamp(3.125rem, 1.31rem + 7.7vw, 6.25rem);
  color: hsl(var(--Primary-300));
}

h3 {
  font-size: clamp(1.5rem, 0.34rem + 4.93vw, 3.5rem);
  color: hsl(var(--Primary-300));
  font-weight: normal;
}

h4 {
  font-size: clamp(1rem, 0.42rem + 2.46vw, 2rem);
  color: hsl(var(--Primary-300), 0.5);
  font-weight: normal;
  text-transform: uppercase;
}

h5 {
  font-family: var(--ff-200);
  font-size: clamp(1rem, 0.57rem + 1.85vw, 1.75rem);
  letter-spacing: 0.16875rem;
  color: hsl(var(--Primary-300), 0.5);
  font-weight: 200;
}
@media (min-width: 48em) {
  h5 {
    letter-spacing: 0.296875rem;
  }
}

body {
  font-family: var(--ff-200);
}

.subheading1 {
  font-family: var(--ff-100);
  font-size: 1.75rem;
  color: hsl(var(--Primary-300));
}

.subheading2 {
  font-family: var(--ff-200);
  font-size: 0.875rem;
  letter-spacing: 0.146875rem;
  color: hsl(var(--Primary-300), 0.7);
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5 {
  margin-top: 0;
  line-height: 1.1;
  margin-bottom: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  overflow-x: hidden;
}

body.active {
  overflow: hidden;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

