}
.header__navigaton {
  position: fixed;
  z-index: 10;
  inset: 0 0 0 30%;
  background-color: hsl(var(--Primary-300), 0.1);
  backdrop-filter: blur(1rem);
  padding-top: 5rem;
  padding-left: 2rem;
  opacity: 0;
  transform: translateX(100%);
  transition: transform 0.05s ease-in-out, opacity 0.1s ease-in-out;
}
@media (min-width: 48em) {
  .header__navigaton {
    position: absolute;
    display: flex;
    transform: translateX(0%);
    opacity: 1;
    padding-top: 0;
    padding-left: 0;
    padding-inline: 3rem;
    height: 6rem;
    width: 28.125rem;
    right: 0;
    inset: initial;
  }
}
@media (min-width: 64em) {
  .header__navigaton {
    position: relative;
    right: -6%;
    width: 50rem;
    height: 6rem;
    backdrop-filter: blur(1.5rem);
  }
}
@media (min-width: 48em) {
  .header__navigaton .nav__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}
@media (min-width: 48em) and (min-width: 64em) {
  .header__navigaton .nav__list {
    justify-content: flex-start;
  }
}
.header__navigaton .nav__list .nav__item {
  position: relative;
}
@media (min-width: 48em) {
  .header__navigaton .nav__list .nav__item {
    height: 100%;
    display: flex;
    align-items: center;
  }
}
@media (min-width: 64em) {
  .header__navigaton .nav__list .nav__item {
    margin-left: 3.5rem;
  }
}
.header__navigaton .nav__list .nav__item .nav__link {
  position: relative;
  margin-top: 1.5rem;
  color: hsl(var(--Primary-300), 0.6);
  display: block;
  font-size: 1rem;
  letter-spacing: 0.16875rem;
  text-transform: uppercase;
}
@media (min-width: 48em) {
  .header__navigaton .nav__list .nav__item .nav__link {
    margin-top: 0rem;
    color: hsl(var(--Primary-300));
    font-size: 0.875rem;
  }
}
@media (min-width: 64em) {
  .header__navigaton .nav__list .nav__item .nav__link {
    font-size: 1rem;
    color: hsl(var(--Primary-300), 0.8);
  }
}
.header__navigaton .nav__list .nav__item .nav__link > span {
  color: hsl(var(--Primary-300));
  font-weight: bold;
}
@media (min-width: 48em) {
  .header__navigaton .nav__list .nav__item .nav__link > span {
    display: none;
  }
}
@media (min-width: 64em) {
  .header__navigaton .nav__list .nav__item .nav__link > span {
    display: inline-block;
  }
}
.header__navigaton .nav__list .nav__item .nav__link[data-active-link]::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 0.25rem;
  background-color: hsl(var(--Primary-300));
  right: 0;
}
@media (min-width: 48em) {
  .header__navigaton .nav__list .nav__item .nav__link[data-active-link]::after {
    display: none;
  }
}
@media (min-width: 48em) {
  .header__navigaton .nav__list .nav__item[data-active-link]::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 0.1875rem;
    background-color: hsl(var(--Primary-300));
    bottom: 0;
  }
}
@media (min-width: 48em) {
  .header__navigaton .nav__list .nav__item[data-hover]::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 0.1875rem;
    background-color: hsl(var(--Primary-300), 0.5);
    bottom: 0;
  }
}
@media (min-width: 87.5em) {
  .header__navigaton::before {
    content: "";
    background-color: hsl(var(--Primary-300), 0.2);
    height: 2px;
    left: -15vw;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
